* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container {
  min-height: 100vh;
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
}
.header {
  grid-column: 1;
  grid-row: 1;
  place-self: center center;
  padding: 16px;
}
.table-wrapper {
  grid-column: 1;
  grid-row: 4;
  height: 100%;
  max-width: 100%;
  padding: 24px 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.map {
  grid-column: 1;
  grid-row: 2;
}
.wrapper {
  width: 100%;
  height: 100%;
  min-height: 50vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
svg {
  width: 100%;
  height: 100%;
  min-height: 50vh;
  overflow: auto;
}
svg .department:hover {
  stroke: black;
  cursor: pointer;
}
@media screen and (min-width: 800px) {
  .container {
    padding: 24px;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto 1fr;
  }
  .map {
    grid-column: 2;
    grid-row: 2;
  }
  .table-wrapper {
    grid-row: 2;
  }
}
